@font-face{font-family:"Exo2.0";src:url(./fonts/Exo2.0-Thin.otf)}.out-of-stock-item,.selected-item,.catalog-item{box-sizing:border-box;border-radius:12px;background-color:#f2f2f2;position:relative;clip-path:polygon(14% 0, 100% 0, 100% 100%, 0 100%, 0 9%);padding:13px 64px 0 47px;width:320px;margin-bottom:10px;cursor:pointer}.out-of-stock-item::before,.selected-item::before,.catalog-item::before{position:relative;content:"";width:65px;display:block;height:4px;top:6px;left:-62px;transform:rotate(-45deg)}.out-of-stock-weight,.selected-weight,.weight{display:flex;justify-content:center;align-items:center;flex-direction:column;font-size:42px;width:80px;height:80px;position:absolute;bottom:15px;left:220px;box-sizing:border-box;border-radius:50%;padding:5px 10px;line-height:40px;color:#fff;margin:0}body{background-image:url("../public/images/Pattern.png");font-family:"Trebuchet MS"}p{margin:0}.box{display:flex;flex-direction:column;align-items:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.title{font-size:36px;color:#fff;font-weight:normal;font-family:"Exo2.0";margin:0 0 30px 0}.catalog{display:flex;gap:80px}.catalog-item{border:4px solid #1698d9}.catalog-item::before{background-color:#1698d9}.catalog-item .additional-description::before{content:"Сказочное заморское яство"}.selected-item{border:4px solid #d91667}.selected-item::before{background-color:#d91667}.selected-item .additional-description::before{content:"Сказочное заморское яство"}.selected-item:hover .additional-description::before{content:"Котэ не одобряет?";color:#d91667}.out-of-stock-item{border:4px solid #b3b3b3}.out-of-stock-item::before{background-color:#b3b3b3}.out-of-stock-item p,.out-of-stock-item h3{color:#b3b3b3}.out-of-stock-item .cat-image{opacity:.5}.out-of-stock-item .additional-description::before{content:"Сказочное заморское яство"}.main-description{font-size:14px;margin-bottom:30px;max-height:32px}.additional-description{margin-bottom:-18px}.additional-description,.main-description{color:#666;width:max-content}.food-title{font-size:24px;margin-bottom:15px}.highlight-food-title{font-size:48px}.cat-image{position:relative;right:47px;top:4px;z-index:-1;border-radius:0 0 50px 8px}.weight{border:1px solid #1698d9;background-color:#1698d9}.selected-weight{border:1px solid #d91667;background-color:#d91667}.out-of-stock-weight{border:1px solid #b3b3b3;background-color:#b3b3b3}.weight-count{position:relative;top:10px}.weight-measurement{font-size:21px}.buy-food{color:#fff;font-size:13px;text-align:center}.highlights-to-buy{color:#1698d9;border-bottom:1px dashed #1698d9;cursor:pointer}@media(max-width: 768px){.box{position:static;transform:translate(0, 0)}.catalog{flex-wrap:wrap-reverse;justify-content:center}}@media(max-width: 426px){.title{text-align:center}.catalog{flex-direction:column;gap:30px}}